@tailwind base;
@tailwind components;
@tailwind utilities;
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.itemCart {
    display: flex;
    border-bottom: 1px solid gray;
    max-width: 500px;
    margin: 1rem auto;
}

.itemCart img {
    width: 200px;
    margin-right: 1rem;
}
.container {
    width: 98%;
    margin: auto;
}

@media screen and (min-width:768px) {
    .container {
        width: 70%;
    }
}

.detail {
    margin-top: 3rem;
    margin-bottom: 3rem;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.detail_image {
    width: 100%;
}

.content {
    width: 100%;
}

@media screen and (min-width:768px) {
    .detail_image {
        width: 50%;
    }
    .content {
        padding-left: 3rem;
        width: 50%;
    }
}
.counter {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
    padding: 1rem;
    text-align: center;
}

.counter button {
    margin: 0.2rem;
}
.film {
    display: block;
    width: 300px;
}
.film img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.scaleCss {
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
